body {
  color: $black-text;
  font-family: Montserrat, sans-serif;
  letter-spacing: $LetterSpacing08;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  min-width: 320px;
}

.container {
  min-width: 320px;
  max-width: 1316px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 480px) {
    width: $phone;
  }

  @media screen and (min-width: 768px) {
    width: $tablet;
  }

  @media screen and (min-width: 1280px) {
    width: $desktop;
  }
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

ol {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: currentColor;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}
